import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons

// core components
import styles from "../../styles/components/cardStyle.js";

const useStyles = makeStyles(styles);

export default function Card(props) {
  const classes = useStyles();
  const { className, variant, children, plain, carousel, chart, ...rest } = props;
  const cardClasses = classNames({
    // [classes.card]: variant !== null ? false : true,
    [classes[variant]]: variant,
    [classes.cardPlain]: plain,
    [classes.cardCarousel]: carousel,
    [classes.cardChart]: chart,
    [className]: className !== undefined,
  });
  return (
    <div className={cardClasses} {...rest}>
      {children}
    </div>
  );
}

// PropTypes
Card.defaultProps = {
  className: "",
  variant: "card",
  plain: false,
  carousel: false,
  chart: false,
  children: [],
};

Card.propTypes = {
  className: PropTypes.string,
  variant: PropTypes.oneOf(["card", "cardLogin", "cardForm", "cardSide", "cardDash"]),
  plain: PropTypes.bool,
  carousel: PropTypes.bool,
  chart: PropTypes.bool,
  children: PropTypes.node,
};
