import React from "react";
// nodejs library that concatenates classes
import classNames from "classnames";
// nodejs library to set properties for components
import PropTypes from "prop-types";
// @material-ui/core components
import { makeStyles } from "@material-ui/core/styles";
// @material-ui/icons

// core components
import styles from "../../styles/components/cardHeaderStyle.js";

const useStyles = makeStyles(styles);

export default function CardHeader(props) {
  const classes = useStyles();
  const { className, children, color, plain, dense, centered, stats, icon, avatar, ...rest } = props;
  const cardHeaderClasses = classNames({
    [classes.cardHeader]: true,
    [classes[color + "CardHeader"]]: color,
    [classes.cardHeaderPlain]: plain,
    [classes.cardHeaderDense]: dense,
    [classes.cardHeaderStats]: stats,
    [classes.cardHeaderIcon]: icon,
    [classes.cardHeaderAvatar]: avatar,
    [className]: className !== undefined
  });
  return (
    <div className={cardHeaderClasses} {...rest} style={{ textAlign: centered ? "center" : "" }} >
      {children}
    </div>
  );
}

// Proptypes

CardHeader.propTypes = {
  className: PropTypes.string,
  color: PropTypes.oneOf(["warning", "success", "danger", "info", "primary", "secondary"]),
  plain: PropTypes.bool,
  dense: PropTypes.bool,
  centered: PropTypes.bool,
  stats: PropTypes.bool,
  icon: PropTypes.bool,
  avatar: PropTypes.bool,
  children: PropTypes.node
};
